@import 'components/variables';

.section-direcciones {
  padding-top: 100px;
  background-image: url(../images/team/bg-team-mobile.png);
  background-position: center top;
  background-repeat: no-repeat;
  background-size: cover;
  @media (min-width: 992px) {
    background: #efeff7;
    background-image: url(../images/team/bg-team.png);
    background-position: center top;
    background-repeat: no-repeat;
    background-size: 100% auto;
    padding-top: 150px;
    padding-bottom: 3rem;
  }
  .content-container {
    background-color: $white;
    padding: 0;
    @media (max-width: 991.98px) {
      max-width: 100%;
    }
    @media (min-width: 576px) {
      border-radius: 25px;
    }
    @media (min-width: 992px) {
      background: $white;
      padding-bottom: 5rem;
    }
  }
  .container1 {
    background: $white;
    border-top-left-radius: 25px;
    border-top-right-radius: 25px;
    @media (min-width: 992px) {
      background: $white;
    }
    @media (max-width: 991.98px) {
      p {
        color: $red;
      }
    }
  }
  .container2 {
    background: $white;
    @media (max-width: 991.98px) {
      max-width: 100%;
      padding-bottom: 4rem;
    }
    @media (min-width: 992px) {
      border-bottom-left-radius: 25px;
      border-bottom-right-radius: 25px;
      padding-bottom: 4rem;
    }
    .col-izq {
      display: none;
      border-right: 1px solid $gray-light;
      @media (min-width: 992px) {
        display: flex;
        justify-content: center;
        align-items: flex-start;
      }
      ul {
        padding-left: 0;
        list-style: none;
        li {
          &:before {
            background: $red;
          }
          &.active {
            &:before {
              left: 0;
              right: 0;
            }
          }
          .items {
            font-weight: bold;
            cursor: pointer;
            color: $black;
            text-decoration: none;
            transition: all 0.3s;
            p {
              margin-bottom: 0;
              padding: .5rem;
            }
            &.close-sesion {
              color: $red;
            }
          }
        }
      }
    }
    .col-der {
      order: 1;
      @media (min-width: 992px) {
        order: 2;
      }
      .address {
        p {
          color: #222;
          font-size: 0.8rem;
        }
        a {
          text-decoration: none;
          font-size: 0.8rem;
          transition: all 0.3s;
          color: $black;
          font-weight: 600;
          &:hover {
            color: $orange;
          }
        }
      }
    }
  }
}

#anadirNueva,
#editarDireccion {
  .modal-dialog {
    .modal-content {
      .modal-header {
        .btn-close {
          &:focus {
            color: #212529;
            background-color: #fff;
            border-color: $red;
            outline: 0;
            box-shadow: none;
          }
        }
      }
      .modal-body {
        .form-control {
          &:focus {
            color: #212529;
            background-color: #fff;
            border-color: $red;
            outline: 0;
            box-shadow: none;
          }
        }
      }
      .modal-footer {
        .btn {
          &:focus {
            outline: 0;
            box-shadow: 0 0 0 0.25rem rgba(253, 69, 13, 0.25);
          }
        }
      }
    }
  }
}
